import React, { useState } from "react";
import FullCalendar from "@fullcalendar/react"; // 
import dayGridPlugin from "@fullcalendar/daygrid"; // 
import timeGridPlugin from "@fullcalendar/timegrid"; // 
import interactionPlugin from "@fullcalendar/interaction"; // 

const MyCalendar = () => {
    const [events, setEvents] = useState([
        // 
        { title: "", start: "2025-01-15", allDay: true },

        // 
        { title: "", start: "2025-01-20T10:00:00", end: "2025-01-20T11:30:00" },
        { title: "", start: "2025-01-20T18:00:00", end: "2025-01-20T19:00:00" },

        // 
        { title: "", start: "2025-01-22", allDay: true, color: "red" },
        { title: "", start: "2025-01-23T14:00:00", end: "2025-01-23T15:30:00", color: "green" },

        // 
        { title: "", start: "2025-01-25", end: "2025-01-28", allDay: true },

        // 
        { title: "", start: "2025-01-16T09:00:00", end: "2025-01-16T10:00:00", daysOfWeek: [4] },

        // 
        { title: "", start: "2025-01-19T15:00:00", end: "2025-01-19T15:30:00", color: "blue" },

        // 
        { title: "", start: "2025-01-30T09:00:00", end: "2025-01-30T11:00:00", description: "" },

        // 
        { title: "", start: "2025-01-20T20:00:00" },
    ]);


    const handleDateClick = (info) => {
        alert(`${info.dateStr}`);
    };
    return (
        <FullCalendar
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            initialView="dayGridMonth" //  ()
            events={events} // 
            editable={true} // 
            selectable={true} // 
            dateClick={handleDateClick} // 
            headerToolbar={{
                left: "prev,next today", // 
                center: "title", // 
                right: "dayGridMonth,timeGridWeek,timeGridDay", // 
            }}
        />
    );
};

export default MyCalendar;
