import React, { useEffect, useState, useContext } from "react";
import "./css/FloatingCircle.css";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import SettingsIcon from '@mui/icons-material/Settings';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import { ThemeContext, LanguageContext } from "../App";
import NightlightIcon from '@mui/icons-material/Nightlight';
import LightModeIcon from '@mui/icons-material/LightMode';
import AbcIcon from '@mui/icons-material/Abc';
import GTranslateIcon from '@mui/icons-material/GTranslate';

export default function FloatingCircle() {
    const [isOpen, setIsOpen] = useState(() => { return localStorage.getItem('isOpen') || 0 }); // 
    const [bottomOffset, setBottomOffset] = useState(20); // 
    const [showScrollToTop, setShowScrollToTop] = useState(false); // 
    const [isClosing, setIsClosing] = useState(false); // 
    const { theme, toggleTheme } = useContext(ThemeContext);
    const { language, toggleLanguage } = useContext(LanguageContext);

    useEffect(() => {
        //  activePage  localStorage
        localStorage.setItem("isOpen", isOpen);
    }, [isOpen]);
    useEffect(() => {
        const handleScroll = () => {
            const currentScrollY = window.scrollY; // 
            const scrollBottom =
                document.documentElement.scrollHeight -
                window.innerHeight -
                window.scrollY;
            if (scrollBottom < 60) {
                setBottomOffset(60); //  60px  60px
            } else {
                setBottomOffset(20); // 
            }

            //  ""  50px 
            setShowScrollToTop(currentScrollY > 50);
        };

        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    const handleScrollToTop = () => {
        window.scrollTo({ top: 0, behavior: "smooth" });
    };
    const toggleSettings = () => {
        if (isOpen == 1) {
            // 
            setIsClosing(true);
            setTimeout(() => {
                setIsOpen(0); // 
                setIsClosing(false);
            }, 300); // 
        } else {
            setIsOpen(1);
        }
    };

    return (
        <div
            className="floating-circle-container"
            style={{ bottom: `${bottomOffset}px` }} // 
        >
            {/*  showScrollToTop  */}

            <div
                className={`floating-circle scroll-to-top-circle ${showScrollToTop ? "visible" : ""
                    }`}
                style={{
                    transform: showScrollToTop
                        ? "translateY(0px)" // 
                        : "translateY(500px)", // 
                    opacity: showScrollToTop ? 1 : 0, // 
                }}
                onClick={handleScrollToTop}
            ><KeyboardArrowUpIcon className="icon" />
            </div>

            {/*  */}
            <div
                className={`floating-circle settings-circle ${isClosing ? "closing" : ""}`}
                onClick={toggleSettings}
            >
                {isOpen == 1 ? <KeyboardDoubleArrowDownIcon className="icon" /> : <SettingsIcon className="icon" />}
            </div>

            {/*  isOpen  */}
            {isOpen == 1 ? (
                <div className={`expanded-circles ${isClosing ? "closing" : ""}`}>
                    {/*  () */}
                    <div className="floating-circle extra-circle">
                        <div
                            onClick={toggleLanguage}
                            style={{
                                backgroundColor: theme === "zh" ? "#555" : "#555",
                                color: theme === "zh" ? "#555" : "#555",
                                borderRadius: "50%",
                                width: "47px",
                                height: "47px",
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                cursor: "pointer",
                            }}
                        >
                            {language === "zh" ? <GTranslateIcon className="icon-GTranslateIcon" /> : <AbcIcon className="icon-AbcIconIcon" />}
                        </div>
                    </div>
                    {/*  () */}
                    <div className="floating-circle extra-circle">
                        <div
                            onClick={toggleTheme}
                            style={{
                                backgroundColor: theme === "light" ? "#fff" : "#555",
                                color: theme === "light" ? "#555" : "#fff",
                                borderRadius: "50%",
                                width: "47px",
                                height: "47px",
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                cursor: "pointer",
                            }}
                        >
                            {theme === "light" ? <LightModeIcon className="icon-LightModeIcon" /> : <NightlightIcon className="icon-NightlightIcon" />}
                        </div>
                    </div>
                    <div className="floating-circle extra-circle">
                        <div
                            onClick={toggleTheme}
                            style={{
                                backgroundColor: theme === "light" ? "#fff" : "#555",
                                color: theme === "light" ? "#555" : "#fff",
                                borderRadius: "50%",
                                width: "47px",
                                height: "47px",
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                cursor: "pointer",
                            }}
                        >
                            {theme === "light" ? <LightModeIcon className="icon-LightModeIcon" /> : <NightlightIcon className="icon-NightlightIcon" />}
                        </div>
                    </div>
                </div>
            ) : null
            }
        </div >
    );
}
